@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&display=swap);
:root {
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --primary-orange-darker: #bd6a0c;
  --primary-dark: rgba(52, 58, 64);
  --primary-black: #000;
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-blue: #3fe3f7;
  --primary-light-gray: #f4f4f4;
  --primary-gray: #ccd1d9;
  --primary-dark-gray: #666666;
  --dark-mode: #121212;
  --about-me-orange: rgb(95, 44, 2);
}

body {
  font-family: "Roboto Condesed", sans-serif !important;
}
/*******************NAVBAR*************************/

nav {
  z-index: 2;
}
.logo {
  width: 4.25rem;
}

/******************NAV LINKS*********************************/

nav {
  background-color: rgba(0, 0, 0, 0.6);
}

nav a.nav-link {
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-right: 0.75rem;
  color: #b0b0b0 !important;
  border-top: 0.1875rem solid rgba(52, 58, 64);
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 4rem;
  -webkit-filter: brightness(75%);
          filter: brightness(75%);
}

@media (max-width: 990px) {
  .navbar-brand {
    width: 7rem;
  }
  .logo {
    width: 3.75rem;
    margin-top: 0.125rem;
  }
}

.navbar-nav > li > a.active,
.navbar-nav > li > a:focus {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

/*******************HEADER**************************/

.header-wrapper {
  position: relative;
  background: url(/Portfolio-Project/static/media/background.f749382e.jpg);
  -webkit-filter: brightness(85%);
          filter: brightness(85%);
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Amatic SC", cursive;
  z-index: 1;
}

.main-info span {
  color: #3fe3f7;
  color: var(--primary-blue);
  text-transform: uppercase;
  font-size: 48px;
}

.typed-text span {
  font-size: 2rem;
  color: #fff;
  color: var(--primary-white);
}

@media (max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }
}

.animate__animated.animate__fadeInBottomRight {
  border: 3px outset #bd6a0c;
  border: 3px outset var(--primary-orange-darker);
  font-size: 16px;
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #fff;
  color: var(--primary-white);
  margin: 2rem;
}

.animate__animated.animate__fadeInBottomRight:hover {
  text-decoration: none;
  background-color: #f33501;
  background-color: var(--primary-hover-red);
  color: #fff;
  color: var(--primary-white);
  transition: 0.2s ease-in-out;
}

.btn-main-offer {
  border: 3px outset #bd6a0c;
  border: 3px outset var(--primary-orange-darker);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #b0b0b0;
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #f33501;
  background-color: var(--primary-hover-red);
  color: #fff;
  color: var(--primary-white);
  transition: 0.2s ease-in-out;
}

/*******************ABOUTME**************************/
.about {
  background-image: linear-gradient(180deg, rgb(37, 9, 1), rgb(22, 15, 13));
}

.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-img {
  width: 16rem;
  border-radius: 50%;
  padding: 0.3rem;
  border: 2px solid #bd6a0c;
  border: 2px solid var(--primary-orange-darker);
}

.about-heading {
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
  font-family: "Amatic SC", cursive;
}

.about p {
  color: #ccd1d9;
  color: var(--primary-gray);
  font-size: x-large;
  z-index: 2;
}

.about-contacts {
  color: #ccd1d9;
  color: var(--primary-gray);
}

@media (max-width: 768px) {
  .about p {
    font-size: medium;
    text-align: center;
  }
}

@media (max-width: 468px) {
  .profile-img {
    width: 10rem;
  }
  .about p {
    font-size: medium;
  }
  .about-heading {
    text-align: center;
  }
}

/*******************MYPAGES**************************/

.services {
  text-align: center;
  padding-bottom: 2rem;
  background: #121212;
  background: var(--dark-mode);
}

.services h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
  font-family: "Amatic SC", cursive;
}

.services .circle {
  position: relative;
  margin: 0.375rem auto;
  background-color: #b0b0b0;
  background-image: linear-gradient(180deg, #121212, #575757 75%);
  border-radius: 10%;
  width: 8.125rem;
  height: 8.125rem;
}

.services .icon {
  font-size: 5rem;
  color: #bd6a0c;
  color: var(--primary-orange-darker);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.services .box {
  height: 16rem;
  border-bottom: 0.125rem solid #bd6a0c;
  border-bottom: 0.125rem solid var(--primary-orange-darker);
  border-right: 0.125rem solid #bd6a0c;
  border-right: 0.125rem solid var(--primary-orange-darker);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: #575757;
  border-radius: 5%;
  transition: 0.3s ease-in-out11;
  z-index: 2;
}

.services .box p {
  color: #fcfcfc;
}

.services .box h3 {
  color: #fcfcfc;
}

.services .box:hover {
  background: #161616;
  border-bottom: 0.3125rem solid #030303;
  border-right: 0.3125rem solid #030303;
  border-left: none;
}

.services .box:hover .icon {
  color: #000;
  color: var(--primary-black);
}

.services .box:hover .circle {
  background: #c2c2c2;
  border-bottom: 0.3125rem solid #050505;
  border-right: 0.3125rem solid #050505;
}

.services .box:hover p {
  color: #b0b0b0;
}

.services .box:hover h3 {
  color: #b0b0b0;
}

/*******************EXPERIENCE**************************/

.experience {
  padding: 0.7rem;
  background: #121212;
}

.experience-wrapper {
  position: relative;
  padding: 3.125rem;
  overflow: hidden;
}

.experience h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
  font-family: "Amatic SC", cursive;
}

.experience ul {
  color: #b0b0b0;
  font-weight: bold;
  font-size: large;
}

.experience li {
  color: #b0b0b0;
  margin-left: 30px;
  font-weight: bold;
  font-size: medium;
}

.experience-wrapper:before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #666666;
  background: var(--primary-dark-gray);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.125rem solid #b0b0b0;
  background: #bd6a0c;
  background: var(--primary-orange-darker);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: #666666;
  color: var(--primary-dark-gray);
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  padding-left: 10px;
  color: #bd6a0c;
  color: var(--primary-orange-darker);
  font-weight: 900;
}
/* -----------------MEDIA--------------- */
@media (max-width: 768px) {
  .experience .container {
    padding-left: 0;
  }

  .experience-wrapper:before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    float: none;
    width: 100%;
  }
  .timeline-block-left {
    float: none;
    direction: ltr;
    width: 100%;
  }
}

/*******************PORTFOLIO**************************/

.portfolio-wrapper {
  background-image: linear-gradient(180deg, rgb(22, 15, 13), #121212);
  padding: 3rem 0;
}

.portfolio-wrapper h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  font-family: "Amatic SC", cursive;
}

.portfolio-image {
  -webkit-filter: opacity(75%);
          filter: opacity(75%);
  width: 15rem;
  height: 15rem;
  box-shadow: 0.3125rem 0.3125rem #050505;
  border-radius: 10%;
}

.portfolio-image-box {
  position: relative;
  margin: 1rem;
}

/*******************OVERFLOW**************************/

.overflow {
  position: absolute;
  opacity: 0;
  background: #000;
  background: var(--primary-black);
  width: 15rem;
  height: 15rem;
  top: 0;
  border: 3px solid #bd6a0c;
  border: 3px solid var(--primary-orange-darker);
  border-radius: 10%;
  cursor: pointer;
}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: #f9ab00;
  color: var(--primary-orange);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.popupbox-content {
  background: #121212;
}

.popup-title {
  text-align: center;
  color: #b0b0b0;
}

.portfolio-image-popupbox {
  display: flex;
  margin: auto;
}

.popupbox-content p {
  color: #b0b0b0;
}

.popupbox-content b {
  color: #b0b0b0;
}

.hyper-link {
  cursor: pointer;
  color: #f9ab00;
  color: var(--primary-orange);
}

.hyper-link:hover {
  color: #ef4035;
  color: var(--primary-red);
}

/* -------------MEDIA-------------- */
@media (max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
    height: 90%;
  }
  .portfolio-image {
    display: flex;
    margin: auto;
  }
}

@media (max-height: 570px) {
  .popupbox-wrapper {
    height: 90%;
  }
  .portfolio-image-popupbox {
    width: 50%;
    height: 60%;
  }
}

@media (max-height: 280px) {
  .popupbox-wrapper {
    height: 75%;
  }
  .portfolio-image-popupbox {
    width: 30%;
  }
}

/*******************CONTACTS**************************/
.contacts {
  background: #121212;
  background: var(--dark-mode);
  padding: 3rem 0;
}

.contacts h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  font-family: "Amatic SC", cursive;
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: #b0b0b0;
  font-size: 1.2rem;
}

.success-message {
  color: #ccd1d9;
  color: var(--primary-gray);
  padding: 0.125rem;
}

.contacts input,
.contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-gray);
  border-radius: 0;
  color: #f9ab00;
  color: var(--primary-orange);
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus,
.contacts textarea:focus {
  background: transparent;
  color: #f9ab00;
  color: var(--primary-orange);
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-gray);
}

.contacts textarea {
  height: 12.2rem !important;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: #f9ab00;
  background: var(--primary-orange);
  display: inline-block;
}

input:focus + .line,
textarea:focus + .line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

@media (max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }

  .contacts .container {
    padding: 0 1rem;
  }

  .contacts p {
    padding: 0 0.4rem;
  }
}

/*******************FOOTER**************************/

.footer {
  background-image: linear-gradient(180deg, #121212 50%, #000);
  background-image: linear-gradient(180deg, #121212 50%, var(--primary-black));
  color: #b0b0b0;
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: #b0b0b0;
}

.footer a:hover {
  text-decoration: none;
  color: #bd6a0c;
  color: var(--primary-orange-darker);
  cursor: pointer;
}

.footer p {
  margin-bottom: 0;
}

.footer .icon {
  color: #b0b0b0;
}

